<template>
  <div v-if="event" class="card shadow-sm">
    <div class="card-body">
      <h5 class="card-title">{{ event.name }}</h5>
      <p class="card-text">{{ event.description }}</p>

      <div>
        <router-link :to="{name: 'EventModify', params: event.id}" class="btn btn-primary me-2" role="button">
          Редактировать
        </router-link>
        <router-link :to="{name: 'TaskAdd', params: event.id}" class="btn btn-success" role="button">
          Добавить задание
        </router-link>
      </div>

      <div>
        <h5>Задания</h5>
        <ul class="list-group">
          <li v-for="task in event.tasks" :key="task.id" class="list-group-item">
            <router-link :to=taskViewLink(task)>{{ task.name }}</router-link>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: 'EventView',
  data() {
    return {
      event: null
    }
  },
  created() {
    this.loadEvent();
  },
  methods: {
    loadEvent() {
      const eventId = this.$route.params.eventId;
      axios.get("/api/admin/events/" + eventId)
        .then(response => {
          console.log(response);
          this.event = response.data;
        })
    },
    taskViewLink(task) {
      return {
        name: "Task",
        params: {
          eventId: task.eventId,
          taskId: task.id
        }
      };
    }
  }
}
</script>
